@use 'sass:map';
@use 'sass:list';
@use 'sass:math';
@use 'sass:color';
@use 'sass:meta';
@use 'sass:string';

// COLORS vs PALETTE FILE
// This file maps everything from the palette file into nicely named variables and maps.
// The Palette file is automatically generated based on a JSON theme. This one is manually edited.

// Quartz 5.3.3
// Bootswatch

$theme: 'quartz' !default;

@import 'palette';
@import 'brand-colors';

// Variables
//
// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.

// The contrast ratio to reach against white, to determine if color changes from "light" to "dark". Acceptable values for WCAG 2.0 are 3, 4.5 and 7.
// See https://www.w3.org/TR/WCAG20/#visual-audio-contrast-contrast
$min-contrast-ratio: 3 !default;

// Customize the light and dark text colors for use in our color contrast function.
$white: #fff !default;
$black: #000 !default;
$color-contrast-dark: $black !default;
$color-contrast-light: $white !default;

$blue: map.get($mojo-blue, hex) !default;
$indigo: map.get($mojo-violet, hex) !default;
$purple: map.get($mojo-violet, light) !default;
$pink: map.get($mojo-red, light) !default;
$red: map.get($mojo-red, hex) !default;
$orange: map.get($mojo-coral, hex) !default;
$yellow: map.get($mojo-yellow, hex) !default;
$green: map.get($mojo-green, hex) !default;
$teal: map.get($mojo-ocean, hex) !default;
$cyan: map.get($mojo-cyan, hex) !default;

// fusv-disable
$blue-100: tint-color($blue, 80%) !default;
$blue-200: tint-color($blue, 60%) !default;
$blue-300: tint-color($blue, 40%) !default;
$blue-400: tint-color($blue, 20%) !default;
$blue-500: $blue !default;
$blue-600: shade-color($blue, 20%) !default;
